@import url(https://fonts.googleapis.com/css?family=Press+Start+2P);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
  font-family: 'Press Start 2P', monospace !important;
}

.App-header{
    display: flex;
    height: 40px;
    background-color: chartreuse;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  
  .header-text{
    font-size: 20px;
    color: blue;
    margin-left: 10px;
  }
  
  .sign-in{
    margin-right: 10px;
  }
.signin-modal{
    position: absolute;
    top: 200px;
    left: 350px;
    right: 350px;
    bottom: 200px;
    background-color: papayawhip;
    border: 10px solid rgb(255, 194, 97);
    border-top-width: 0;
    border-left-width: 0;  
    display:flex;
}

.signin-overlay{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(2, 0, 3, 0.514);
}
.ret-btn{
    background-color: transparent;
    font-size: 20px;
    border: none;
    color: red;
    text-shadow: 2px 2px 0 #641ee6;
}
